.first__page {
  min-height: calc(100vh - 108px);
}
.informative__heading {
  text-align: center;
  margin: 50px 0 20px 0;
  line-height: 50px;
  font-weight: $font-medium;

  .informative__blue {
    color: $darkcontrast;
  }
}

.front__wrapper {
  display: flex;
  flex-direction: column;
  font-size: 1.1em;
}

.informative {
  max-width: 1200px;

  .informative__subheader {
    display: none;
    text-align: start;
    font-weight: $font-normal;
    line-height: 30px;
  }
}

@media (min-width: 600px) {
  .informative__heading {
    font-size: 2em;
  }
}

@media (min-width: 1200px) {
  .front__wrapper {
    flex-direction: row;
    justify-content: center;
  }

  .informative__heading {
    font-size: 2.2em;
    line-height: 50px;
  }
  .informative {
    width: 40vw;

    .informative__subheader {
      margin: 60px 20px 0 20px;
      display: block;
    }
  }
}
