.min-height {
    min-height: 100vh;


    .terms__main {
        text-align: center;
        max-width: 1200px;
        margin: 0 auto;

        .terms__header {

            .terms__heading {
                margin: 40px 0;
                font-size: 2.1em;
            }

            .terms__subheader {
                font-size: 1.4em;
                font-weight: $font-normal;
            }
        }

        .terms {
            margin: 60px 20px;
            list-style: none;
            text-align: left;

            .term__big{
                font-size: 1.4em;
                font-weight: $font-bold;
                margin: 30px 0;
            }

            .term {
                margin: 15px;
                font-size: 1.1em;
            }
        }
    }
}