.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 7vh;
  background-color: $darkcontrast;

  .logo {
    height: 45px;

    .logo__img {
      height: 100%;
    }
  }

  .navbar__list {
    display: flex;
    width: 50%;
    justify-content: space-evenly;
    z-index: 2;
    margin-left: 20px;

    .navbar__item {
      list-style: none;

      .navbar__link {
        font-weight: $font-medium;
        text-decoration: none;
        color: $white;
        letter-spacing: 2px;
        transition: 0.3s;
        font-size: 0.9em;
      }
    }
  }

  .burger {
    display: none;

    .burger__line {
      display: block;
      width: 28px;
      height: 3px;
      border-radius: 2px;
      margin: 5px;
      background-color: $contrast;
    }
  }
}

@media (max-width: 800px) {
  body,
  html {
    overflow-x: hidden;
    position: relative;
  }

  .navbar {
    .navbar__list {
      flex-direction: column;
      align-items: center;
      position: absolute;
      right: 0px;
      height: 93vh;
      width: 100%;
      top: 7vh;
      background-color: $darkcontrast;
      color: $darkcontrast;
      transform: translateX(100%);
      transition: all 0.4s ease-in;

      .navbar__item {
        opacity: 0;

        .navbar__link {
          color: $contrast;
          font-size: 0.9em;
        }
      }
    }

    .burger {
      display: block;
      cursor: pointer;

      .burger__line {
        transition: all 0.4s ease;
      }
    }
  }
}

.navbar__list.navbar__list--active {
  transform: translateX(0%);
}

.toggle .burger__line--line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .burger__line--line2 {
  opacity: 0;
}

.toggle .burger__line--line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

@keyframes navlinkFade {
  from {
    opacity: 0;
    transform: translateX(250px);
  }

  to {
    opacity: 1;
    transform: translateX(0px);
  }
}
