.params {
  .param__form {
    .param__wrap {
      display: flex;
      flex-direction: column;
      margin: 0 auto;
      width: 70vw;

      .param__label {
        font-size: 1.1em;
        margin-bottom: 3px;
      }

      .param__value {
        height: 40px;
        font-size: 0.9em;
        margin-bottom: 10px;
        padding: 0 7px;
        border: 1px solid rgb(170, 170, 170);
      }

      .red {
        border-bottom: 2px solid red;
      }

      .age__input-error {
        display: none;
        color: red;
      }
      .weight__input-error {
        display: none;
        color: red;
      }
      .height__input-error {
        display: none;
        color: red;
      }

      .active-error {
        font-size: 0.8rem;

        display: block;
        margin-bottom: 7px;
      }

      #gender__value,
      #activity__level {
        cursor: pointer;
        background-color: white;
      }

      .calculate__btn {
        margin: 40px auto;
        border-radius: 12px;
        border: none;
        background-color: $darkcontrast;
        color: #fff;
        font-size: 1em;
        padding: 0.8em 2.8em;
        cursor: pointer;
        transition: 0.3s;

        &:focus {
          outline: none;
        }
      }
    }
  }
}

@media (min-width: 1200px) {
  .params {
    .param__form {
      .param__wrap {
        width: 30vw;
        margin: 50px 100px;

        .calculate__btn {
          margin: 30px auto;
        }
        .param__value {
          font-size: 1em;
        }
      }
    }
  }
}
